
  html,body
  {
    --color-1: #1c679e;
    --color-2: #6dbf41;
    --color-3: #4e9ace;
    --color-4: #dddddd;
    --color-5: #090909;
    --color-6: #ffffff;
    --body-font: "ABeeZee";
    --title-font: "Fredoka One";
    background-color: var(--color-3);
    -moz-user-select: text;
     -ms-user-select: text;
         user-select: text;
    -webkit-user-select: text;
    font-family: var(--body-font);
    margin: 0px;
    padding: 0px;
    overflow-x: hidden;
  }

  svg {
      fill: var(--color-1);
      height: 32px;
      width: 32px;
  }


/** HEADER BAR SECTION **/

.app-logo {
  height: 80px;
  width: auto;
  padding: 5px 40px 5px 20px;
}

#Header-Bar{
	position: fixed;
	top: 0;
	background-color: var(--color-6);
	width: 100%;
	color: var(--color-1);
	padding: 1%;
	cursor: default;
  z-index: 20;
}

#Mobile-Header-Bar{
	position: relative;
	top: 0;
	background-color: var(--color-6);
	width: 100%;
	color: var(--color-1);
  height: 100%;
	padding: 1%;
	cursor: default;
  height: auto;
  display: block;
  z-index: 20;
}

.LinkBar{
  position: fixed;
	right: 4%;
	top: 4%;
  padding: 5px 20px 5px 20px;
}

.MobileLinkBar{
  text-align: left;
	display: block;
  background: var(--color-6);
  margin: auto;
  padding: 10px;
  padding-bottom: 0px;
  -webkit-transition-property: all;
  -o-transition-property: all;
  transition-property: all;
	-webkit-transition-duration: 1s;
	     -o-transition-duration: 1s;
	        transition-duration: 1s;
	-webkit-transition-timing-function: cubic-bezier(0, 1, 0.1, 1);
	     -o-transition-timing-function: cubic-bezier(0, 1, 0.1, 1);
	        transition-timing-function: cubic-bezier(0, 1, 0.1, 1);
}

.MobileLinkBarClosed{
  -webkit-transition-property: all;
  -o-transition-property: all;
  transition-property: all;
	-webkit-transition-duration: 1s;
	     -o-transition-duration: 1s;
	        transition-duration: 1s;
	-webkit-transition-timing-function: cubic-bezier(0, 1, 0.1, 1);
	     -o-transition-timing-function: cubic-bezier(0, 1, 0.1, 1);
	        transition-timing-function: cubic-bezier(0, 1, 0.1, 1);
  height: 0px;
  overflow: hidden;
  text-align: left;
  display: block;
  background: var(--color-6);
  margin: auto;
  padding-left: 10px;
}

.mobile-link-text{
  display: inline;
  position: relative;
  top: -5px;
  padding-left: 30px;

}

.LinkItem{
	font-size: 1.15em;
	cursor: pointer;
	display: inline-block;
  padding-left: 20px;
  padding-right: 20px;
  height: 100%;
  width: 100px;
  text-align: center;
}


.MobileLinkItem{
	font-size: 2.1em;
	cursor: pointer;
  display: inline;
  position: relative;
}

.menu-button{
  position: absolute;
  right: 5%;
  top: 24px;
  width: 50px;
  height: 50px;
  border-style: solid;
  border-radius: 20%;
  border-color: var(--color-1);
  border-width: 2px;
}



.menu-icon{
  position: absolute;
  top: calc(50% - 16px);
  left: calc(50% - 16px);
  height: 32px;
  width: 32px;
}

.mobile-link-icon{
  position: relative;
  top: calc(50% - 16px);
}

.mobile-link-icon-hide{
  fill: var(--color-6) !important;
}

.LinkItem:hover, .menu-button:hover, .MobileLinkItem:hover, .x-button:hover {
	-webkit-animation: Link-fade 1s ease-out;
	        animation: Link-fade 1s ease-out;
	color: var(--color-2);
  border-color: var(--color-2);
  background-color:
}

.MobileLinkItem:active, .menu-button:active{
	-webkit-animation: Link-fade 0.1s ease-out;
	        animation: Link-fade 0.1s ease-out;
	color: var(--color-2);
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  border-color: var(--color-2);
}


/** HOME PAGE TILE   **/

.home-tile{
  font-style: var(--body-font);
  color: var(--color-6);
  font-size: 1.45em;
  margin: auto;
  text-align: center;
  margin-bottom: 50px;
  margin-top: 4%;
  padding: 6px;
}

.home-title{
  font-size: 1.5em;
  font-family: var(--title-font);
}

/** INFORMATION TILES **/


.info-tile{
  margin: auto 5%;
  margin-bottom: 100px;

  padding: 20px;
  -webkit-animation: fadeIn .5s;
          animation: fadeIn .5s;

  position: relative;
  z-index: 0;
}

.title-space{
  position: absolute;
  top: 50px;
  margin: auto;
  font-size: 5em;
  width: 25%;
  display: inline-block;
  text-align: center;

  font-family: var(--title-font);

}

.tile-description{
  position: relative;
  top: 0px;
  left: 30%;
  display: inline-block;
  width: 67%;
  font-size: 2em;
  border-left-width: 5px;
  border-left-style: solid;
  padding: 30px;
}


    /** Mobile Info Tiles **/

.mobile-info-tile{
  margin-top: 5%;
  margin-bottom: 5%;
  font-size: 1em;
  -webkit-animation: fadeIn .5s;
          animation: fadeIn .5s;
  width: 100%;
  z-index: 0;
}

.mobile-title-space{
  font-size: 2em;
  text-align: center;
  height: 20%;
  font-family: var(--title-font);
  padding: 10px 0px 10px 0px;
}

.mobile-tile-description{
  display: block;
  font-size: 1.2em;
  border-top-width: 5px;
  border-top-style: solid;

  padding: 20px 10px 20px 10px;
}


/** STEP BY STEP MENU FOR FORMS **/

.step-tile{
  margin: 5%;
  max-width: 2000px;
  font-size: 1.2em;
  color: var(--color-6);
  background-color: var(--color-1);
  padding: 4%;
  min-height: 200px;
  -webkit-animation: fadeIn .5s;
          animation: fadeIn .5s;
  position: relative;
  z-index: 0;
}

.step-menu-button-box{
  text-align: center;
  margin: auto;
  -webkit-animation: fadeIn .5s;
          animation: fadeIn .5s;
}

.step-menu-button{
  background-color: var(--color-3);
  color: var(--color-6);
  display: inline-block;
  padding: 20px;
  border-style: solid;
  border-color: var(--color-2);
  border-width: 2px;
  margin-right: 5vw;
  margin-top: 4vw;
  -webkit-animation: fadeIn .5s;
          animation: fadeIn .5s;
  cursor: pointer;
}

.form-menu-description{
  -webkit-animation: fadeIn .5s;
          animation: fadeIn .5s;
  font-size: 1.5em;
}

.form-menu-subtext{
  margin-top: 4vw;
  -webkit-animation: fadeIn .5s;
          animation: fadeIn .5s;
}

.x-button{
  position: absolute;
  text-align: center;
  top: 2%;
  right: 1%;
  height: 40px;
  width: 40px;
  cursor: pointer;
}

.x-button-mobile{
  text-align: center;
  position: absolute;
  top: 1%;
  right: 1%;
  height: 30px;
  width: 30px;
  cursor: pointer;
}

a {
  color: inherit;
  text-decoration: none;
}

/** SERVICE TAB SECTION **/


.my-tabs{
  display: -ms-inline-flexbox;
  display: inline-flex;
  position: relative;
  top: -15px;
  left: -20px;
  background-color: inherit;
  color: var(--color-6);
}

.my-tab{
  color: var(--color-6);
  margin-right: 10px;
}

.my-tab[aria-selected="true"]{
  background-color: var(--color-2);
  -webkit-animation: TabFade 0.5s;
          animation: TabFade 0.5s
}

.my-tabs-mobile{

  display: inline;
}

.my-tablist-mobile{
  margin-bottom: 10px;
}


.my-tab-mobile{
  width: 20%;
  color: var(--color-6);
}


.panel-title{
  font-size: 1.5em;
  font-family: var(--title-font);
  margin-bottom: 20px;
  -webkit-animation: fadeIn 1s;
          animation: fadeIn 1s;
}

.panel-content{
  -webkit-animation: fadeIn .5s;
          animation: fadeIn .5s;
}

/** LOCATION TAB SECTION **/

.my-loc-tab{
  color: var(--color-6);
  margin-right: 10px;
}


.my-loc-tab[aria-selected="true"]{
  background-color: var(--color-3);
  -webkit-animation: TabFade2 0.5s;
          animation: TabFade2 0.5s;
}


.my-loc-tab-mobile{
  width: 50%;
  margin-right: 0px;
  font-size: 1em;
}

.list-content{
  list-style-type: none;
  padding: 10px;
}

.list-item-mobile{
  font-size: 1em;

}

li{
  margin-bottom: 20px;
}

.contact-bar{
  background-color: var(--color-6);
  width: 100%;
  display: inline-block;
  color: var(--color-1);
  position: relative;
  vertical-align: middle;
}

.credits-text{
  display: inline-block;
  position: absolute;
  right: 5px;
  top: 5px;
  max-width: 30%;
  font-family: var(--body-font);
  font-weight: bold;
  font-size: 0.7em;
  text-align: right;
  cursor: pointer;

}


.bottom-logo{
  display: inline-block;
  height: 50px;
  width: 120px;
  padding: 10px;
}

.bottom-link{
  text-decoration: underline;
}

.button-to-top{
  display: inline-block;
  position: absolute;
  margin: auto;
  top: 20%;
  left: 45%;
  text-align: center;
  font-size: 1em;
  font-weight: bold;
}


  /** ANIMATIONS **/

@-webkit-keyframes fadeIn {
  from {
    opacity: 0
  }
}

@keyframes fadeIn {
  from {
    opacity: 0
  }
}

@-webkit-keyframes Link-fade {
  from { color: inherit;
          border-color: inherit}
  to { color: var(--color-2);
        border-color: var(--color-2)}
}

@keyframes Link-fade {
  from { color: inherit;
          border-color: inherit}
  to { color: var(--color-2);
        border-color: var(--color-2)}
}

@-webkit-keyframes TabFade {
  from {
          background-color: inherit;
        }
  to {
          background-color: var(--color-2)
        }
}

@keyframes TabFade {
  from {
          background-color: inherit;
        }
  to {
          background-color: var(--color-2)
        }
}

@-webkit-keyframes TabFade2 {
  from {
          background-color: inherit;
        }
  to {
          background-color: var(--color-3)
        }
}

@keyframes TabFade2 {
  from {
          background-color: inherit;
        }
  to {
          background-color: var(--color-3)
        }
}



